import React from 'react';
import { jsPDF } from 'jspdf';
import * as XLSX from 'xlsx';

const Exportacao = ({ funcionarios }) => {

  // Funo para exportar para PDF
  const exportToPDF = () => {
    const doc = new jsPDF();
    doc.setFontSize(18);
    doc.text('Lista de Funcionrios', 14, 20);

    let y = 30; // Comear na linha 30

    // Cabealho do PDF
    doc.setFontSize(12);
    doc.text('Nome', 14, y);
    doc.text('Funo', 60, y);
    doc.text('Salrio', 120, y);
    doc.text('Admisso', 160, y);
    doc.text('Demisso', 210, y);
    
    y += 10; // Pular uma linha

    // Dados dos funcionrios
    funcionarios.forEach((funcionario) => {
      doc.text(funcionario.nome, 14, y);
      doc.text(funcionario.funcao, 60, y);
      doc.text(funcionario.salario, 120, y);
      doc.text(funcionario.admissao, 160, y);
      doc.text(funcionario.demissao || 'No aplicvel', 210, y);
      y += 10;
    });

    doc.save('funcionarios.pdf');
  };

  // Funo para exportar para Excel
  const exportToExcel = () => {
    const ws = XLSX.utils.json_to_sheet(funcionarios.map((funcionario) => ({
      Nome: funcionario.nome,
      Funo: funcionario.funcao,
      Salrio: funcionario.salario,
      Admisso: funcionario.admissao,
      Demisso: funcionario.demissao || 'No aplicvel',
    })));
    
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Funcionrios');
    
    // Gerar e baixar o arquivo Excel
    XLSX.writeFile(wb, 'funcionarios.xlsx');
  };

  return (
    <div>
      <button onClick={exportToPDF} style={{ backgroundColor: '#FF5722' }}>
        Exportar para PDF
      </button>
      <button onClick={exportToExcel} style={{ backgroundColor: '#4CAF50', marginLeft: '10px' }}>
        Exportar para Excel
      </button>
    </div>
  );
};

export default Exportacao;
