import React, { useState, useEffect } from 'react';
import './App.css';
import FormularioCadastro from './FormularioCadastro'; // Componente do formulrio
import BuscaFuncionarios from './BuscaFuncionarios'; // Componente de busca
import Exportacao from './Exportacao'; // Componente de exportao
import ListaDeFuncionarios from './ListaDeFuncionarios'; // Componente para lista de funcionrios
import Swal from 'sweetalert2'; // Para alertas

function App() {
  const [funcionarios, setFuncionarios] = useState([]); // Lista de funcionrios
  const [showCadastro, setShowCadastro] = useState(false); // Controle da exibio do cadastro
  const [showBusca, setShowBusca] = useState(false); // Controle da exibio da busca
  const [showLista, setShowLista] = useState(false); // Controle da exibio da lista
  const [showExportacao, setShowExportacao] = useState(false); // Controle da exibio da exportao
  const [showLgpdModal, setShowLgpdModal] = useState(false); // Controle do modal LGPD

  // Carregar os funcionrios do LocalStorage ao iniciar a aplicao
  useEffect(() => {
    const funcionariosSalvos = JSON.parse(localStorage.getItem('funcionarios')) || [];
    setFuncionarios(funcionariosSalvos);
  }, []);

  // Salvar os funcionrios no LocalStorage sempre que a lista mudar
  useEffect(() => {
    if (funcionarios.length > 0) {
      localStorage.setItem('funcionarios', JSON.stringify(funcionarios));
    }
  }, [funcionarios]);

  // Funo para remover um funcionrio
  const removerFuncionario = (id) => {
    setFuncionarios(funcionarios.filter(funcionario => funcionario.id !== id));
    Swal.fire('Sucesso', 'Funcionrio removido com sucesso!', 'success');
  };

  // Funo para editar um funcionrio
  const editarFuncionario = (id, nome, funcao, salario, admissao, demissao) => {
    setFuncionarios(funcionarios.map(funcionario => 
      funcionario.id === id ? { ...funcionario, nome, funcao, salario, admissao, demissao } : funcionario
    ));
    Swal.fire('Sucesso', 'Funcionrio editado com sucesso!', 'success');
  };

  // Funo para fechar o modal da LGPD
  const closeLgpdModal = () => {
    setShowLgpdModal(false);
  };

  // Funo para limpar o localStorage e resetar o estado
  const limparStorage = () => {
    localStorage.clear();  // Limpa o localStorage
    setFuncionarios([]);   // Limpa a lista de funcionrios no estado
    Swal.fire('Sucesso', 'Todos os dados foram removidos!', 'success');
  };

  return (
    <div className="App">
      <h1>Cadastro de Funcionrios</h1>

      {/* Container adicional com uma descrio */}
      <div className="info-container">
        <p>Bem-vindo ao sistema de gerenciamento de funcionrios. Aqui voc pode cadastrar, editar, buscar e exportar informaes dos funcionrios.</p>
      </div>

      {/* Botes para mostrar as sees */}
      <div className="section-buttons">
        <button style={{ margin: "10px" }} onClick={() => setShowCadastro(!showCadastro)}>
          {showCadastro ? 'Esconder Cadastro' : 'Mostrar Cadastro'}
        </button>
        <button style={{ margin: "10px" }} onClick={() => setShowBusca(!showBusca)}>
          {showBusca ? 'Esconder Busca' : 'Mostrar Busca'}
        </button>
        <button style={{ margin: "10px" }} onClick={() => setShowLista(!showLista)}>
          {showLista ? 'Esconder Lista' : 'Mostrar Lista'}
        </button>
        <button style={{ margin: "10px" }} onClick={() => setShowExportacao(!showExportacao)}>
          {showExportacao ? 'Esconder Exportao' : 'Mostrar Exportao'}
        </button>

        {/* Boto para limpar o localStorage */}
        <button style={{ margin: "10px" }} onClick={limparStorage}>Limpar Dados</button>
      </div>

      {/* Exibindo as sees com base nos estados */}
      {showCadastro && (
        <div className="area-formulario">
          <h2>Cadastro de Funcionrio</h2>
          <FormularioCadastro setFuncionarios={setFuncionarios} funcionarios={funcionarios} />
        </div>
      )}

      {showBusca && (
        <div className="area-busca">
          <h2>Busca de Funcionrios</h2>
          <BuscaFuncionarios funcionarios={funcionarios} />
        </div>
      )}

      {showLista && (
        <div className="area-lista">
          <h2>Lista de Funcionrios Cadastrados</h2>
          <ListaDeFuncionarios 
            funcionarios={funcionarios}
            removerFuncionario={removerFuncionario}
            editarFuncionario={editarFuncionario}
          />
        </div>
      )}

      {showExportacao && (
        <div className="area-exportacao">
          <h2>Exportar Dados</h2>
          <Exportacao funcionarios={funcionarios} />
        </div>
      )}

      {/* Boto "O que  LGPD?" */}
      <button className="lgpd-btn" onClick={() => setShowLgpdModal(true)}>
        O que  LGPD?
      </button>

      {/* Modal LGPD */}
      {showLgpdModal && (
        <div className="lgpd-modal">
          <div className="lgpd-modal-content">
            <h2>O que  LGPD?</h2>
            <p>
              A Lei Geral de Proteo de Dados (LGPD)  uma legislao brasileira que regula
              o tratamento de dados pessoais de indivduos no Brasil. Ela foi criada com o objetivo
              de garantir a privacidade e a proteo dos dados pessoais, estabelecendo regras para
              a coleta, armazenamento, tratamento e compartilhamento de informaes.
            </p>
            <p>
              A LGPD exige que empresas e organizaes tratem os dados de forma transparente e
              responsvel, e que forneam aos indivduos mais controle sobre seus dados pessoais.
            </p>
            <button onClick={closeLgpdModal}>Fechar</button>
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
