import React, { useState } from 'react';
import Swal from 'sweetalert2';

const FormularioCadastro = ({ setFuncionarios, funcionarios }) => {
  const [nome, setNome] = useState('');
  const [funcao, setFuncao] = useState('');
  const [salario, setSalario] = useState('');
  const [admissao, setAdmissao] = useState('');
  const [demissao, setDemissao] = useState('');

  // Funo para validar o salrio
  const validarSalario = (salario) => {
    return !isNaN(salario) && parseFloat(salario) > 0;
  };

  // Funo para adicionar ou editar funcionrio
  const handleSubmit = (event) => {
    event.preventDefault();

    // Valida o salrio
    if (!validarSalario(salario)) {
      Swal.fire('Erro', 'Salrio invlido. Deve ser um nmero positivo.', 'error');
      return;
    }

    const novoFuncionario = {
      id: Date.now(), // Gerando um id nico com base no timestamp
      nome,
      funcao,
      salario: `R$ ${parseFloat(salario).toFixed(2)}`,
      admissao,
      demissao,
    };

    setFuncionarios([...funcionarios, novoFuncionario]);

    // Limpar o formulrio aps o envio
    setNome('');
    setFuncao('');
    setSalario('');
    setAdmissao('');
    setDemissao('');
    Swal.fire('Sucesso', 'Funcionrio cadastrado com sucesso!', 'success');
  };

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <label>Nome:</label>
        <input
          type="text"
          value={nome}
          onChange={(e) => setNome(e.target.value)}
          placeholder="Digite o nome do funcionrio"
          required
        />
      </div>

      <div>
        <label>Funo:</label>
        <input
          type="text"
          value={funcao}
          onChange={(e) => setFuncao(e.target.value)}
          placeholder="Digite a funo"
          required
        />
      </div>

      <div>
        <label>Salrio:</label>
        <input
          type="number"
          value={salario}
          onChange={(e) => setSalario(e.target.value)}
          placeholder="Digite o salrio"
          required
        />
      </div>

      <div>
        <label>Data de Admisso:</label>
        <input
          type="date"
          value={admissao}
          onChange={(e) => setAdmissao(e.target.value)}
          required
        />
      </div>

      <div>
        <label>Data de Demisso (se aplicvel):</label>
        <input
          type="date"
          value={demissao}
          onChange={(e) => setDemissao(e.target.value)}
        />
      </div>

      <button type="submit">Cadastrar Funcionrio</button>
    </form>
  );
};

export default FormularioCadastro;
